:root {
    --primary-color: #c5231b;

    /* dark mode */
    --bg-color: rgb(30, 30, 30);
    --card-top-color: rgb(50, 50, 50);
    --card-bottom-color: rgb(40, 40, 40);
    --header-menu-color: rgb(40, 40, 40);
    --h2-bg-color: rgb(0, 0, 0, 0.5);
    --h2-text-color: rgb(183, 183, 183);
    --h2-outline-color: rgb(102, 102, 102);
    --side-text-color: rgb(102, 102, 102);
    --yellow-text-color: rgb(191, 144, 0);
    --h3-text-color: rgb(239, 239, 239);
    --text-color-2: rgb(153, 153, 153);
    --pages-text-color: rgb(217, 217, 217);
    --desc-text-color: rgb(217, 217, 217);
    --weight-roboto-thin: 100;
    --weight-roboto-light: 300;
    --weight-roboto-regular: 400;
    --weight-roboto-medium: 500;
    --weight-roboto-bold: 700;
    --weight-roboto-black: 900;
    /* light mode */
}

body {
    background-color: var(--bg-color);
    /*color: white;*/
    font-family: "Roboto", Arial, sans-serif;
    font-style: normal;
    margin: 0;
    padding: 0;
}

header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    justify-content: left;
    background-color: var(--header-menu-color);
}

a.home {
    display: block;
    position: relative;
    margin-right: 20px;
    margin-left: 20px;
}

a.home .text-logo {
    height: 40px;
    margin-top: auto;
    margin-bottom: auto;
    position: relative;
    display: block;
}

nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

nav ul li {
    padding: 0;
    height: 60px;
}

nav ul li a {
    text-decoration: none;
    color: var(--pages-text-color);
    text-align: center;
    align-content: center;
    height: 100%;
    padding: 0 30px;
    font-size: 18px;
    display: block;
}

nav ul li a:hover {
    background-color: var(--bg-color);
}

.profile-menu {
    position: relative;
    margin-left: auto;
}

.profile-menu button {
    background: none;
    border: none;
    font-size: 18px;
    cursor: pointer;
    margin-right: 10px;
}

.profile-menu button img {
    width: 40px;
    height: 40px;
    border-radius: 100px;
}

.dropdown-content {
    display: none;
    position: absolute;
    right: 0;
    background-color: #333;
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
    z-index: 1;
}

.dropdown-content a {
    color: white;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}

.dropdown-content a:hover {
    background-color: #444;
}

.event-day {
    margin: 20px 60px;
    box-shadow: 0 4px 6px rgb(0, 0, 0, 0.5);
}

.event-day h2 {
    background-color: var(--h2-bg-color);
    color: var(--h2-text-color);
    padding: 16px 20px;
    margin: 0;
    border: 0.8px var(--h2-outline-color) solid;
    font-size: 20px;
}

.events-card {
    background: linear-gradient(var(--card-top-color), var(--card-bottom-color));
    padding: 10px 10px;
    margin-top: 10px;
    cursor: pointer;
}

.event {
    display: flex;
    align-items: center;
    padding: 2px 0px;
    overflow-x: hidden;
    position: relative;
}

.event-logo img {
    width: 40px;
    height: 40px;
    margin-right: 15px;
}

.event-info {
    flex-grow: 1;
    display: flex;
    align-items: center;
    font-size: 14px;
    color: var(--h2-text-color);
    position: relative;
    width: 100px;
    margin-right: 600px;
    /*overflow: hidden;*/
}

.event-details {
    min-height: 70px;
    max-height: 250px;
    display: none;
    max-width: 700px;
    margin: 5px 0 10px 0px;
    position: relative;
    border-left: 4px var(--bg-color) solid;
}

img.event-banner-background {
    opacity: 0.3;
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
}

div.desc {
    color: var(--desc-text-color);
    margin: 0;
    font-size: 14px;
    padding: 0px 10px;
    height: 100%;
    opacity: 1;
    position: relative;
}

div.desc img.location-icon {
    width: 18px;
    height: 18px;
    opacity: 1;
    filter: brightness(0.9);
    position: absolute;
    left: 8px;
    top: 8px;
    margin: 0;
}

div.desc p.description {
    white-space: pre-wrap;
    height: 900px;
    position: relative;
    padding-top: 40px;
    padding-bottom: 10px;
    height: auto;
    width: 100%;
    overflow: hidden;
    font-size: 15px;
}

div.desc p.location {
    font-weight: var(--weight-roboto-medium);
    color: var(--desc-text-color);
    top: 8px;
    left: 30px;
    margin: 0;
    white-space: pre;
    position: absolute;
}

.event-details div {
    left: 0;
    right: 0;
    max-height: 200px;
    background: linear-gradient(var(--card-top-color), transparent);
    position: relative;
}

.event h3 {
    margin: 0;
    color: var(--h3-text-color);
    font-weight: var(--weight-roboto-light);
    margin-right: 20px;
    font-size: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    max-width: 260px;
    width: 260px;
    min-width: 200px;
    flex-shrink: 0;
    flex-grow: 1;
    flex-basis: 0px;
}

.event h3#priority {
    font-weight: var(--weight-roboto-regular);
}

.event-info p {
    color: var(--text-color-2);
    font-weight: var(--weight-roboto-light);
    font-size: 14px;
    margin-left: 50px;
    min-width: 100px;
    width: 100px;
    white-space: nowrap;
}

.event-info p.members-only {
    color: var(--yellow-text-color);
}

.time {
    color: var(--h2-text-color);
    font-weight: var(--weight-roboto-bold);
    border: 1px var(--primary-color) solid;
    padding: 2px 0px;
    width: 80px;
    min-width: 80px;
    text-align: center;
    margin: 10px;
    border-radius: 100px;
}

.time#time-passed {
    background-color: var(--primary-color);
}

footer {
    width: 100%;
    bottom: 0;
    z-index: -2;
    position: relative;
}

footer img {
    width: 100%;
    height: auto;
    position: relative;
}

footer div.bottom {
    height: 100px;
    margin-top: -4px;
    background-image: url(../images/footer-bottom.png);
}

footer p {
    color: var(--h2-text-color);
    text-align: center;
    margin-top: -30px;
}

.event-day {
    width: calc(30% + 150px);
}

.events-card {
    padding: 0;
}

.event {
    height: 100px;
    margin-bottom: 0;
}

.event-container {
    padding: 0;
    margin: 0;
}

img.event-banner-background {
    opacity: 0.6;
}

.calendar-placeholder {
    height: 500px;
    outline: rgb(112, 112, 112) solid 2px;
    margin-left: 50px;
    margin-right: 50px;
    margin-bottom: 50px;
    top: 50px;
}

.event div.background-gradient {
    background: linear-gradient(transparent, var(--card-top-color));
    width: 100%;
    height: 100%;
    position: absolute;
}

.event .event-logo img {
    width: 60px;
    height: 60px;
    z-index: 3;
    opacity: 1;
    position: absolute;
    left: 20px;
    top: 20px;
}

.event h3 {
    color: white;
    font-weight: var(--weight-roboto-medium);
    font-size: 25px;
    min-width: calc(100% - 200px);
    height: 50px;
    top: 15px;
    left: 100px;;
    position: absolute;
    z-index: 3;
}

.dropdown-logo {
    position: absolute;
    width: 30px;
    height: 30px;
    right: 30px;
    top: 38px;
    z-index: 3;
}

.organizations-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: left;
    width: calc(60% + 412px);
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
}

div.client-button-panel {
    background-color: var(--card-top-color);
    width: 130px;
    height: 45px;
    position: absolute;
    z-index: 5;
    border-radius: 10px;
    left: 100px;
    top: 50px;
    box-shadow: 0 4px 6px rgb(0, 0, 0, 0.5);
}

div.client-button-panel img {
    width: 30px;
    height: 30px;
    position: absolute;
    top: calc(50% - 15px);
}

div.client-button-panel img.save-icon {
    left: 15px;
}

div.client-button-panel img.external-icon {
    right: 15px;
}

.event-details {
    max-width: 100%;
    border-left: none;
    display: none;
    margin-top: 0;
    margin-bottom: 0;
    max-height: none;
    padding-bottom: 50px;
    background: linear-gradient(var(--card-top-color), var(--card-bottom-color));
}

.event-details div {
    background: none;
    margin-top: 0;
    max-height: none;
}

div.desc {
    position: relative;
}

p.location, img.location-icon {
    position: absolute !important;
    top: -75px !important;
}

p.description {
    position: absolute !important;
    top: -100px !important;
}
