.search-filter-container {
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: left;
    margin-left: 150px;
}

#search-bar {
    width: 30%;
    padding: 10px;
    padding-left: 20px;
    border-radius: 25px;
    border: 1px solid var(--h2-outline-color);
    background-color: var(--h2-bg-color);
    color: white;
    outline: none;
    font-size: 16px;
}

.filter-options {
    margin-top: 10px;
    display: flex;
    align-items: center;
}

.filter-options span {
    margin-right: 10px;
    color: #666;
    margin-top: 12px;
    vertical-align: middle;
    font-weight: 500;
    font-size: 17px;
}

.filter-options label {
    margin-right: 15px;
    color: var(--pages-text-color);
}

.radio-item {
    display: inline-block;
    position: relative;
    padding: 0 6px;
    margin: 10px 0 0;
  }
  
  .radio-item input[type='radio'] {
    display: none;
  }
  
  .radio-item label {
    color: #666;
    font-weight: normal;
    cursor: pointer;
  }
  
  .radio-item label:before {
    content: " ";
    display: inline-block;
    position: relative;
    top: 5px;
    margin: 0 5px 0 0;
    width: 16px;
    height: 16px;
    border-radius: 100px;
    border: 1.5px solid;
    background-color: transparent;
  }
  
  .radio-item input[type=radio]:checked + label:after {
    border-radius: 100px;
    width: 16px;
    height: 16px;
    position: absolute;
    top: 6.5px;
    left: 7.5px;
    content: " ";
    display: block;
    background: var(--primary-color);
  }