:root {
  --primary-color: #c5231b;

  /* dark mode */
  --bg-color: rgb(30, 30, 30);
  --card-top-color: rgb(50, 50, 50);
  --card-bottom-color: rgb(40, 40, 40);
  --header-menu-color: rgb(40, 40, 40);
  --h2-bg-color: rgb(0, 0, 0, 0.5);
  --h2-text-color: rgb(183, 183, 183);
  --h2-outline-color: rgb(102, 102, 102);
  --side-text-color: rgb(102, 102, 102);
  --yellow-text-color: rgb(191, 144, 0);
  --h3-text-color: rgb(239, 239, 239);
  --text-color-2: rgb(153, 153, 153);
  --pages-text-color: rgb(217, 217, 217);
  --desc-text-color: rgb(217, 217, 217);
  --weight-roboto-thin: 100;
  --weight-roboto-light: 300;
  --weight-roboto-regular: 400;
  --weight-roboto-medium: 500;
  --weight-roboto-bold: 700;
  --weight-roboto-black: 900;
  /* light mode */
}

body {
  background-color: var(--bg-color);
  /*color: white;*/
  font-family: "Roboto", Arial, sans-serif;
  font-style: normal;
  margin: 0;
  padding: 0;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  justify-content: left;
  background-color: var(--header-menu-color);
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

nav ul li {
  padding: 0;
  height: 60px;
}

.sidebar {
  width: 180px; 
  height: 110px; 
  align-items: center;
  background-color: rgb(40, 40, 40); 
  border-radius: 10px;
  transition: height 0.2s ease;
}

.bartext {
  font-family: "Roboto", Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  padding-bottom: 0px;
  color: rgb(217, 217, 217);
  text-decoration: none;
  text-align: center;
}

.container {
  display: flex;
  height: calc(100vh-50px ); /* Assuming the header is 60px high */
  padding: 20px;
  text-align: center;
}

.left-pane {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
} 

.calendartype {
  font-weight: normal;
  font-family: Tahoma;
  color: rgb(145, 145, 145);
  background-color: rgb(31, 35, 36);
  width: 120px;
  margin-bottom: 50px;
}

.select {
  width: 200px; 
  padding: 8px; 
  font-size: 16px; 
  border: 1px; 
  border-radius: 5px;
}

.option {
  padding: 8px;
  font-size: 16px;
}

.eventbar {
  margin-left: auto; 
  margin-top: 20px; 
}

.addevent {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 9px 12px;
  gap: 8px;
  height: 40px;
  width: 180px;
  border: none;
  background: #c5231b;
  border-radius: 20px;
  cursor: pointer;
}

.addevent:hover {
  background: #e52e26;
}

.label {
  line-height: 22px;
  font-size: 16px;
  color: #fff;
  font-family: sans-serif;
  letter-spacing: 1px;
}

/* Main Calendar (React-Big-Calendar) */
.main-calendar {
  flex: 1;
  background-color:  var(--header-menu-color);
  border-radius: 10px;
  padding: 20px;
  color: var(--pages-text-color);
  font-family: Tahoma, sans-serif;
  margin-left: 20px; /* Add spacing between the calendar and the side elements */
}

.rbc-event {
  background-color: #c5231b !important; /* Change to your desired color */
  height: 30px;
  color: white !important; /* Text color */
  border: none !important; /* Remove border if needed */
}

/* Optional: Style event labels */
.rbc-event-content {
  color: white !important; /* Text color inside events, switch to impoprtant to override */
}

/* Hide the agenda button */
.rbc-toolbar button:nth-of-type(4) {
  display: none;
}

/* Style for today's date */
.rbc-today {
  background-color: var(--header-menu-color) !important;
}

/* Style for the number text in today's date cell */
.rbc-today span {
  color: #c5231b !important; /* Change the number text color to white */
}

.rbc-date-cell.rbc-now{
  color: #c5231b;
}

/* Style for days before the start of the current month */
.rbc-off-range-bg {
  background-color:  rgb(32, 32, 32) !important;/* Change to your desired color */
}